$baseTextColor: #4d5969;

.search-instruments {
  width: 350px;
  height: 90px;
  &__input-field {
    width: 320px;
    height: 34px;
    border: 1px solid #d8e2ee;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 18px;
    outline: none;
    text-indent: 5px;
    padding-left: 30px;
    color: $baseTextColor;
  }
  &__input-btn {
    position: relative;
    left: 14px;
    top: 32px;
    width: 16px;
    height: 16px;
    border: none;
    background: url(../../../../asserts/search.png) no-repeat 50% 50%;
    cursor: pointer;
  }
}
